.contact {
    color: var(--text-black-900);
    &-box {
        width: calc(100% - 30px);
        margin: 30px 15px 0px;
        border: $border-settings;
        border-radius: $border-radius;
        background: var(--bg-black-900);
        display: flex;
        overflow: hidden;
        &__content {
            width: 55%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 40px 25px;
        }
        &__title {
            margin-bottom: 20px;
            font-size: 24px;
            font-weight: 700;

        }    
        &__input {
            display: contents;
            & textarea {
                resize: none;
                height: 70px;
            }
            & p {
                font-size: 18px;
            }
            & input[type=date]::-webkit-calendar-picker-indicator{
                opacity: 0;
            }

        }
        &__field{
            display: block;
            font-family: 'Source Sans Pro', sans-serif;
            margin: 10px 0 20px;
            padding: 8px 10px;
            border: 2px solid var(--text-black-700);
            border-radius: $border-radius-small;
            background: transparent;
            color: var(--text-black-700);
            transition: all .4s;
            width: 100%;
            cursor: pointer;
            &::placeholder {
                font-family: 'Source Sans Pro', sans-serif;
                color: var(--text-black-700);
            }
            &:hover, &:focus {
               background: linear-gradient(90deg, var(--skin-color) 0%, rgba(255,0,134,0.9473039215686274) 81%);
               box-shadow: var(--box-shadow-form);
               outline: none;
               color: white;
               transform: scale(1.05);
               &::placeholder{
                color: white;
               }
            }
            
        }
        &__select {
            padding: 8px 10px;
            margin: 10px 0 20px;
            background: transparent;
            border: 2px solid var(--text-black-700);
            border-radius: $border-radius-small;
        }
        &__img {
            width: 45%;
            background: var(--skin-color);
            & img {
                height: 90%;
            }
        }
        &__file {
            height: 28px;
            width: calc(50% - 10px);
            margin: 10px 0 20px;
            border: 2px dashed var(--text-black-700);
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
        }
        &__icon {
            position: absolute;
            bottom: 0;
            right: 0;
            transform: translate(-10px, -30px);
            transition: .4s all;
        }
        label {
            position: relative;
            & .contact-box__field:hover + .contact-box__icon, .contact-box__field:focus + .contact-box__icon {
                color: white;
                transform: translate(-10px, -30px) scale(1.05); 
            }
        }
    }   
    & .btn-form {
        max-width: 150px;
        width: 100%;
        padding: 10px;
        font-size: 16px;
        cursor: pointer;
        border: 2px solid var(--text-black-700);
        border-radius: 4px;
        color: var(--text-black-900);
        background: var(--bg-black-900);
        font-weight: 700;
        transition: all .4s;
        &:hover {
           
            transform: scale(1.05);
            color: white;
        }
        &.submit:hover {
            background: linear-gradient(90deg, rgba(215,232,39,1) 0%, rgba(109,253,29,1) 50%, rgba(69,245,252,1) 100%);
        }
        &.reset:hover {
            background: linear-gradient(90deg, rgba(253,29,29,1) 0%, rgba(232,194,39,1) 100%);
        }
    }

}

@media (min-width: 2000px) {
    .contact {
        &-box {
            margin: 45px 15px 0;
            &__content {
                padding: 60px 25px;
            }
            &__title {
                margin-bottom: 35px;
                font-size: 28px;
            }
            &-box__input {
                & p {
                    font-size: 24px;
                }
            }
            &__field {
                font-size: 18px;
                padding: 14px 10px;
            }
            & i { 
                width: 25px;
                height: 25px;
            } 
            &__file {
                height: 50px;
            }
        }
        & .btn-form {
            max-width: 250px;
            font-size: 20px;
            padding: 15px;
        }
        
    }
}

@media (max-width: 1000px) {
    .contact {
        & .direction-column  {
            width: calc(100% - 30px)!important;
        }
        &-box {
            &__file {
                height: 35px;
                width: calc(100% - 30px);
                font-size: 28px;
            }
        }
        & .button-container {
            flex-direction: row;
        }
        & .btn-form {
            margin: 0 15px;
        }
    }
}

@media (max-width: 768px) {
    .contact {
        &-box {
            flex-direction: column-reverse;
            &__file {
                height: 35px;
                width: calc(100% - 30px);
                font-size: 28px;
            }
            &__content {
                width: 100%;
            }
            &__img {
                width: 100%;
                height: 300px;
                & img {
                    height: 100%;
                    width: 100%;
                }
            }
        }
        & .button-container {
            flex-direction: row;
        }
        & .btn-form {
            margin: 0 15px;
        }
    }
}

@media (max-width: 450px) {
    .contact {
        &-box__title {
            font-size: 21px;
        }
        & .flex-between {
            flex-direction: column!important;
        }
        & .btn-form {
            margin: 10px 0;
            width: calc(100% - 30px);
            max-width: none;
        }
    }
}