:root {
    /**bg-colors**/
    --bg-black-900: #f2f2fc;
    --bg-black-100: #fdf9ff;
    --bg-black-50: #e8dfec;
    --box-shadow-form:  0px 0px 35px -7px rgba(0,0,0,0.66);

    /**text-colors**/
    --text-black-900: #302e4d;
    --text-black-700: #504e70;

}
:root .dark{
    /**bg-colors**/
    --bg-black-900: #060505!important;
    --bg-black-100: #1b161e!important;
    --bg-black-50: #4c4c4c!important;
    --box-shadow-form:  0px 0px 35px -7px rgb(255, 255, 255);
    
    /**text-colors**/
    --text-black-900: #e9e9e9!important;
    --text-black-700: #f2f2f2!important;
}
.home {
    padding-left: 270px;
}
.flex-between {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.direction-column {
    display: flex;
    flex-direction: column;
    width: calc(50% - 15px);
}
.section {
    background: var(--bg-black-900);
    min-height: 100vh;
    display: flex;
    padding: 0 30px;
    & .container {
        padding: 50px 0 60px;
    }
    &-title {
        flex: 0 0 100%;
        max-width: 100%;
        @include pad-15;
        margin-top: 15px;
        & h2 {
            font-size: 40px;
            font-weight: 600;
            color: var(--text-black-900);
            position: relative;
            &::after {
                @include titleDecor;
                width: 5%;
                bottom: -15px;
            }
            &::before{
                @include titleDecor;
                width: 10%;
                bottom: -5px;
            }
        }
    }
}

.grid {
    display: grid!important;
    justify-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    @include pad-15;
    margin: 60px -15px 0;
}

.hidden, .non-active {
    display: none!important;
}
.container {
    max-width: 1140px;
    width: 100%;
    margin: auto;
} 
.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px!important;
    margin-right: -15px!important;
    position: relative;
}

.btn {
    font-size: 18px;
    padding: 15px 30px;
    background: var(--skin-color);
    text-decoration: none;
    border-radius: 16px;
    color: rgb(238, 233, 233);
    transition: all .4s;
    cursor: pointer;
    &:hover {
        box-shadow: 10px 5px 5px var(--skin-color);
        background-color: #f2f2fc;
        color: #302e4d;
        border: 1px solid var(--skin-color);
        & a {
            color: #504e70;
        } 

    }
    & a {
        color: #f2f2fc;
    }

}

.img-fluid {
    max-width: 100%;
    height: auto;
}

.screen-hider {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.622);
    z-index: 5;
    &.active {
        display: block;
    }
}

@media (min-width: 2000px) {
    .section h2{
        font-size: 52px;
    }
    .home {
        padding-left: 400px;
    }
    .container {
        max-width: 1800px;
    }
    .btn {
        font-size: 28px;
    }
}

@media (max-width: 1000px) {
    .grid {
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 800px) {
    .section {
        &-title {
            & h2 {
                &::after {
                    width: 10%;
                }
                &::before{
                    width: 20%;
                }
            }
        }
    }
    
}

@media (max-width: 600px) {
    .grid {
        grid-template-columns: 1fr;
    }
}

