.portfolio {
    color: var(--text-black-900);
    align-items: stretch;
    justify-content: center;
    position: relative;
    & .section-title {
        margin-bottom: 40px;
    }
    & .swiper {
        margin-top: 60px;
    }
    &-slider {
        display: flex;
        position: relative;
        transition: transform 0.3s ease;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        left: 0;
        margin-bottom: 80px;
    }
    &-box {
        position: relative;
        height: 350px!important;
        width: calc(33.33% - 30px)!important;
        @include boxShadow;
        display: flex!important;
        align-items: center;
        justify-content: center;
        transition: all .4s;
        flex-shrink: 0;
        &:hover {
            background-color: #fdf9ff;
        }
        &__decor {
            position: absolute;
            width: 60px;
            height: 60px;
            visibility: hidden;
            transition: all .2s;
            z-index: -5;
            &.diogram {
                top: 27px;
                left: 25px;
                transform: translate(-21px, 10px) rotate(21deg);
                
            }
            &.table {
                color: rgb(4, 58, 184);
                right: 25px;
                bottom: 27px;
                transform: translate(25px, 1px) rotate(330deg);

            }
            &.document {
                left: 109px;
                top: 30px;
                transform: translate(-93px, 0) rotate(346deg);
            }
            &.loop {
                right: 88px;
                bottom: 24px;
                transform: translate(81px, 10px);
            }
            &.net {
                right: 50px;
                top: 24px;
                transform: translate(44px, 0) rotate(14deg);
            }
            &.laptop {
                left: 57px;
                bottom: 16px;
                transform: translate(-53px, 0) rotate(344deg);
            }
        }
        & svg {
            opacity: 0.4;
            fill: var(--skin-color);
        }

        &.r:hover {
            background-color: rgba(62, 111, 226, 0.842);
            & svg {
                fill: rgb(4, 58, 184);
                opacity: 1;
            }
            & .portfolio-box__decor {
                visibility: visible;
                z-index: 1;
                opacity: .6;
                &.diogram {
                    transform: translate(0, 0) rotate(21deg);
                }
                &.table {
                    transform: translate(0, 0) rotate(332deg);
                }  

            }
        }

        &.sql:hover {
            background-color: rgba(80, 231, 46, 0.793);
            & svg {
                fill: rgb(43, 156, 8);
                opacity: 1;
                z-index: 1;
            }
            & .portfolio-box__decor {
                visibility: visible;
                z-index: 1;
                opacity: .6;
                &.document {
                    transform: translate(0, 0) rotate(346deg);
                }
                &.loop {
                    transform: translate(0, 0);
                }
            }
        }

        &.js:hover {
            background-color: rgba(247, 255, 5, 0.818);
            & svg {
                fill: rgb(200, 177, 3);
                opacity: 1;
            }
            & .portfolio-box__decor {
                visibility: visible;
                z-index: 1;
                opacity: .6;
                &.net{
                    transform: translate(0, 0) rotate(14deg);
                }
                &.laptop {
                    transform: translate(0, 0) rotate(344deg);
                }
            }
        }

    }
    &-collection {
        display: none;
        transition: .4s all;
    }
}

.layer {
	overflow: hidden;
    width: calc(50% - 30px);
    border-radius: $border-radius;
    &__container {
        perspective: 1000px;
        height: 300px;
    	transform-style: preserve-3d;
    	transform: rotateX(var(--move-y)) rotateY(var(--move-x));
    	will-change: transform;
    	transition: transform $transition;
        border-radius: $border-radius;
        box-shadow: inset 0px -148px 109px -23px rgba(15,13,15,0.85);
    }
    &__item {
    	position: absolute;
    	inset: 0;
    	background-size: cover;
    	background-position: center;
    	display: flex;
    	align-items: end;
        border-radius: $border-radius;
    	justify-content: space-around;
    }
    &-1 {
    	transform: translateZ(-55px) scale(1.06);
    }
    &-2 {
    	transform: translateZ(100px) scale(.88);
        align-items: center;
        & h3 {
            font-size: 72px;
            font-weight: 600;
            color: #f2f2fc;
            align-items: center;
        } 
    }
    &-3 {
    	transform: translateZ(200px) scale(.9);
        & .btn {
            margin-bottom: 40px;
            max-width: 150px;
            width: 100%;
            text-align: center;
        }
    }
}


.layer-4, .layer-5, .layer-6 {
	pointer-events: none;
}

@media (min-width: 2000px) {
    .portfolio { 
        &-box {
            min-height: 400px;
            &__decor {
                width: 75px;
                height: 75px;
            }
        }
        &__icon {
            width: 100px;
            height: 100px;
        }
        .layer {
            &__container {
                height: 400px;
                box-shadow: inset 0 -200px 350px -23px rgba(15,13,15,.85);
            }
            &-3 .btn {
                margin-bottom: 65px;
                max-width: 250px;
                padding: 20px 30px
            }
        }
    }
}

@media (max-width: 1000px) {
    .portfolio {
        &-box {
            margin: 15px;
            width: calc(50% - 30px)!important;
        }
    }
    .flex-between {
        flex-direction: column;
        margin: 0 -15px;
    }
    .layer {
        width: calc(100% - 30px);
        &__container {
            height: 400px;
            margin: 15px 0;
            box-shadow: inset 0px -248px 159px -23px rgba(15,13,15,0.85);
        }
    }
}

@media (max-width: 940px) {
    .portfolio {
        min-width: 100vw;
        & .container {
            max-width: 840px;
        }
        & .slider {
            margin-top: 30px;
        }
    }
    .layer-2 {
        text-align: center;
        & h3 {
            font-size: 52px;
        }
    }
    .layer-3 {
        transform: translateZ(200px) scale(0.7);
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;
        & .btn {
            margin-bottom: 15px;
            
        }
    }
}

@media (max-width: 740px) {
    .portfolio-box {
        width: calc(100% - 30px)!important;
    }
}
