.main {
    display: flex;
    color: var(--text-black-900);
    &-info {
        flex: 0 0 60%;
        max-width: 60%;
        @include pad-15;
        &__title {
            font-size: 26px;
            margin: 15px 0 30px;
            & span {
                font-size: 30px;
                font-weight: 600;
                color: var(--skin-color);
            }
        }
        &__intro {
            font-size: 26px;
            margin: 0 0 30px;
            & .type-animation {
                color: var(--skin-color);
                font-weight: 600;
            }
        }
        &__text {
            font-size: 22px;
            color: var(--text-black-900);
            margin: 0 0 70px;
            line-height: 150%;
        }
    }
    &-img {
        flex: 0 0 40%;
        max-width: 40%;
        text-align: center;
        @include pad-15;
        position: relative;
        img{
            max-height: 400px;
            border-radius: 5px;
        }
        &::before {
            content: "";
            position: absolute;
            height: 80px;
            width: 80px;
            border-top: 8px solid var(--skin-color);
            border-left: 8px solid var(--skin-color);
            top: 0;
            left: 0;
            transform: translate(50%, -50%)
        }
        &::after{
            content: "";
            position: absolute;
            height: 80px;
            width: 80px;
            border-bottom: 8px solid var(--skin-color);
            border-right: 8px solid var(--skin-color);
            bottom: 0;
            right: 0;
            transform: translate(-50%, 50%)
        }
   }
}

@media (min-width: 2000px) {
    .main {
        &-info__title {
            font-size: 40px;
            margin: 15px 90px 60px 0;
            & span {
                font-size: 40px;
            }
        }
        &-info__intro {
            font-size: 32px;
            margin: 0 0 60px;
        }
        &-info__text  {
            font-size: 26px;
            margin: 0 40px 140px 0;
        }
        &-img { 
            & img {
                max-height: 600px;
            }
            &::before {
                height: 122px;
                width: 122px;
                transform: translate(60%,-50%);
            }
            &::after {
                height: 122px;
                width: 122px;
                transform: translate(-60%,50%);
            }
        }
    }
}

@media (max-width: 1200px) {
    .main {
        & .row {
            flex-direction: column;
            align-items: center;
            
        }
        &-info {
            text-align: center;
            max-width: 840px;
            &__text {
                margin: 0 0 40px;
            }
        }
        &-img {
            margin: 100px 0 0 0;
            max-width: 100%;
            &::after{
                transform: translate(50%, 50%)
            }
            &::before{
                transform: translate(-50%, -50%)
            }
        }
    }
}

@media (max-width: 950px) {
    .main {
        min-width: 100vw;
        &-info {
            max-width: 500px;
        }
    }
}

@media (max-width: 500px) {
    .main {
        &-img {
            &::after {
                transform: translate(-10%, 5%);
            }
            &::before {
                transform: translate(10%, -5%);
            }
        }
    }
}
