.services {
    color: var(--text-black-900);
    & .container {
        padding: 50px 0 40px;
    }
    &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        @include boxShadow;
        margin: 0 15px 20px;
        width: calc(100% - 30px);
    }
    &-icon {
        width: 100px;
        height: 100px;
        fill: var(--skin-color)!important;
        margin: 20px;
    }
    &-title {
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 15px;
    }
    &-info {
        margin: 0 35px 20px;
        text-align: center;
        height: 20%;
    }
}

@media (min-width: 2000px) {
    .services {
        &-icon {
            width: 100px;
            height: 100px;
            margin: 45px 0 35px;
        }
        &-title {
            font-size: 28px;
            font-weight: 600;
            margin-bottom: 25px;
        }
        &-info {
            font-size: 24px;
            margin: 0 35px 60px;
        }
    }
}


@media (max-width: 940px) {
    .services {
        min-width: 100vw;
    }
}