.gear {
    position: fixed;
    right: 0;
    display: flex;
    z-index: 5;
    transform: translate(calc(100% - 45px), 0);
    transition: all .4s;
    &.active {
        transform: translate(0, 0);
        & .gear-svg {
            animation: 4s linear 0s infinite rotation;
        }
    }
    &-button {
        width: 50px;
        height: 40px;
        margin: 10px 10px 0 0;
        box-shadow: $box-shadow;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-black-100);
        border: $border-settings;
        border-radius: $border-radius;
    }
    &-svg {
        fill: var(--skin-color);
        cursor: pointer;
        width: 25px;
        height: 25px;

        &:hover {
            animation: 4s linear 0s infinite alternate rotation;
        }
        
        @keyframes rotation {
          from {
            transform: rotate(0deg);
          }
          to {
            transform: rotate(360deg);
          }
        }
    }
    &-block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        height: auto;
        max-width: 275px;
        width: 100%;
        margin: 10px 10px 0 0;
        padding: 10px 20px;
        background: var(--bg-black-100);
        border: $border-settings;
        border-radius: $border-radius;
        &.active {
            box-shadow: $box-shadow;
        }
        &__title {
            margin-bottom: 10px;
            font-size: 16px;
            font-weight: 600;
            color: var(--text-black-700);
        }
        &__color, &__language {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 15px;
            &-icon {
                width: 25px;
                height: 25px;
                border-radius: 50%;
                background: var(--skin-color);
                cursor: pointer;
                &.red {
                    background: #ec1839;
                }
                &.green {
                    background: #31ec18;
                }
                &.blue {
                    background: #143cee;
                    
                }
                &.purple {
                    background: #9718ec;
                }
            }
        }
        &__toggle {
            display: flex;
            align-items: center;
            background:  var(--bg-black-50);
            border-radius: 35px;
            position: relative;
            border: $border-settings;
            height: 30px;
            overflow: hidden;
            max-width: 60px;
            width: 100%;
            margin-bottom: 15px;
            & .ball {
                position: absolute;
                cursor: pointer;
                width: 30px;
                height: 30px;
                background: var(--text-black-700);
                border-radius: 50px;
                transform: translate(0, 0px);
                transition: all .4s;
                &::before {
                    content: url('assets/img/moon.svg');
                    fill: var(--bg-black-900);
                }
                &.day {
                    transform: translate(27px, 0px);
                    &::before {
                        content: url('assets/img/sun.svg');
                        
                    }
                }
            }
        }
        &__language {
            justify-content: flex-start!important;
        }
        &__l {
            display: block;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            overflow: hidden;
            margin-right: 10px;
            & img {
                width: 40px;
                height: 40px; 
            }
        }
    }
}

@media (min-width: 2000px) {
    .gear {
        transform: translate(calc(100% - 65px),0);
        &-button {
            width: 80px;
            height: 60px;
        }
        &-svg {
            width: 40px;
            height: 50px;
        }
        &-block {
            max-width: 275px;
            padding: 15px 25px;
            &__title {
                font-size: 20px;
            }
            &__toggle {
                height: 35px;
            }
            &__color-icon {
                width: 35px;
                height: 35px;
            }
        }
    }
}