
$box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
$border-settings: 3px solid var(--text-black-900);
$border-radius: 14px;
$border-radius-small: 4px;
$transition: 1.5s cubic-bezier(.05, .5, 0, 1);


@mixin frame {
    content: "";
    position: absolute;
    height: 25px;
    width: 25px;
    margin: -15px -20px;
}

@mixin sidebarButton {
    position: absolute;
    width: 45px;
    height: 2px;
    background: var(--skin-color);
    display: inline-block;
    left: 50%;
    transform: translate(-50%, 0);
    transition: all .4s;
}

@mixin pad-15 {
    padding-left: 15px;
    padding-right: 15px;
}

@mixin titleDecor {
    content: "";
    position: absolute;
    height: 3px;
    background: var(--skin-color);
    left: 0;
}

@mixin aboutPoint {
    content: "";
    background: var(--skin-color);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(20px, 0);
}

@mixin boxShadow {
    background-color: var(--bg-black-100);
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    margin: 0 15px;
    border-radius: $border-radius-small;
}