
.sidebar {
    max-width: 270px;
    width: 100%;
    height: 100vh;
    background: var(--bg-black-100);
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 10;
    transition: all .4s;
    &-title {
        font-size: 42px;
        position: absolute;
        letter-spacing: 4px;
        top: 50px;
        left: 50%;
        transform: translate(-50%,0);
        & a {
            text-decoration: none;
            font-weight: 600;
            position: relative;
            color: var(--text-black-900);
            &::before {
                @include frame;
                border-bottom: 5px solid var(--skin-color);
                border-left: 5px solid var(--skin-color);
                bottom: 0;
                left: 0;
            }
            &::after {
                @include frame;
                border-top: 5px solid var(--skin-color);
                border-right: 5px solid var(--skin-color);
                top: 0;
                right: 0;
            }
            & span {
                font-family: 'Clicker Script', cursive;
                font-size: 40px;
            }
        }
    }
    &-button {
        position: relative;
        margin: 10px 0 0 10px;
        background: var(--bg-black-100);
        cursor: pointer;
        width: 60px;
        height: 50px;
        display: none;
        transition: all .4s;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        border: $border-settings;
        border-radius: $border-radius;
        & span {
            @include sidebarButton;
            bottom: 45%;
        }
        &::before {
            content: "";
            @include sidebarButton;
            bottom: 30%;
            
        }
        &::after {
            content: "";
            @include sidebarButton;
            top: 35%;
        }
    }
    &-nav {
        margin-top: 50px;
        li{
            display: block;
            margin-bottom: 35px;
        }
        &__item {

            font-size: 22px;
            font-weight: 600;
            display: block;
            border-bottom: 2px solid var(--bg-black-50);
            color: var(--text-black-900);
            padding: 5px 15px;
            &.active {
                color: var(--skin-color);
            }
            & i {
                width: 25px;
                height: 25px;
                margin-right: 15px;
            }
            &:hover {
                color: var(--skin-color);
            }

        }
        &__img {
            width: 35px;
            height: 35px;
            margin: 0 10px 0 0;
        }
    }
}

@media (min-width: 2000px) {
    .sidebar {
        max-width: 400px;
        &-title {
            font-size: 52px;
            & a span {
                font-size: 60px;
            }
        }
        &-nav {
            margin-top: 0;
            &__item {
                font-size: 28px;
                padding: 25px 15px 5px;
                & i {
                    margin-right: 25px;
                }
            }
        }

    }
}

@media (max-width: 1350px) {
    .home {
        padding: 0;
    }
    .sidebar {
        opacity: 0;
        overflow: hidden;
        transform: translate(-100%, 0);
        &-button {
            display: block;
            position: fixed;
            z-index: 100;
        }
    }
    .sidebar.active {
        opacity: 1;
        overflow: visible;
        transform: translate(0, 0);
    }
    .sidebar-button.active {
        transform: translate(300px, 0);
        & span {
            width: 0;
        }
        &::after {
            transform: translate(-50%,0) rotate(45deg);
            top: 48%;
        }
        &::before {
            transform: translate(-50%,0) rotate(-45deg);
            bottom: 45%;
        }
    }
    .home.active {
        background-color: black;
    }
}

@media (max-height: 480px) {
    .sidebar {
        &-title {
            top: 10px;
            font-size: 30px;
            & span {
                font-size: 25px;
            }
        }
        &-nav {
            margin-top: 75px;
            & li {
                margin-bottom: 20px;
            }
            &__item { 
                font-size: 18px;
            }
        }
        &-title {
            a {
                &::after{
                    content: none;
                }
                &::before {
                    content: none;
                }
            }
        }
    }
}
