.about {
    color: var(--text-black-900);
    & .row {
        justify-content: space-between;
    }
    &-text {
        margin: 45px 0 30px;
        font-size: 24px;
        color: var(--text-black-900);
        line-height: 150%;
    }
    &-info {
        @include pad-15;
        margin: 0 0 70px;
        width: 43.33%;
        &__title{
            margin: 30px 0;
            font-size: 24px;
            border-bottom: 2px solid var(--skin-color);
            & span {
                font-weight: 600; 
            }
        }
    }
    &-box {
        width: calc(50% - 30px);
        @include boxShadow;
        &__title{
            text-align: center;
            margin: 20px;
            font-size: 24px;
            font-weight: 600;

        }
        &__content {
            padding: 0 60px 35px;
            font-size: 20px;
            position: relative;
            &::before {
                @include aboutPoint;
                width: 2px;
                height: calc(100% - 35px);
            }
        }
        &__info {
            margin-bottom: 35px;
            position: relative;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                @include aboutPoint;
                height: 22px;
                width: 22px;
                transform: translate(-50px, 0);
                border-radius: 50%;

            }
        }
        &__date {
            font-weight: 700;
            margin-bottom: 10px;
            font-size: auto;
            & i {
                margin-right: 10px;
            }
        }
        &__subtitle {
            margin-bottom: 10px;
            font-size: 22px;
            font-weight: 500;
        }


    }
}

@media (min-width: 2000px) {
    .about  {
        &-text {
            margin: 45px 0 30px;
            font-size: 30px;
        }
        &-info__title {
            margin: 40px 0;
            font-size: 28px;
        }
        &-box {
            &__info {
                margin-bottom: 45px;
            }
            &__title {
                margin: 25px;
                font-size: 28px;
            }
            &__subtitle {
                margin-bottom: 15px;
                font-size: 26px;
            }
            &__text {
                font-size: 24px;
            }
        }
    }
}

@media (max-width: 1200px) {
    .about {
        &-info {
            width: 100%;
            margin: 0 0 0;
            &:last-child {
                margin: 0 0 40px;
            }
            &__title:last-child {
                margin: 30px 0 0;
            }
        }
    }
}

@media (max-width: 950px) {
    .about {
        min-width: 100vw;   
        & .row {
            flex-direction: column;
            
        }
        &-box {
            width: calc(100% - 30px);
            &:first-child {
                margin: 30px 15px;
            }
        }
        &-text {
            text-align: justify;
        }
    }
}

@media (max-width: 500px) {
    .about {
        &-img {
            &::after {
                transform: translate(-10%, 5%);
            }
            &::before {
                transform: translate(10%, -5%);
            }
        }
    }
}

